/* _______________ PAGE INDEX _______________ */
.top {
  background-image: url(../img/content/bg.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 150px 0 90px 0;
  margin-bottom: 74px;
  color: var(--colorWhite);

  &__heading {
    margin-top: 30px;
    margin-bottom: 62px;
    font-size: 6.4rem;
    line-height: 7.8rem;
    font-weight: 700;
    text-align: left;
    max-width: 832px;
  }

  &__text {
    font-size: 33px;
    line-height: 40px;
    font-weight: 400;
    text-align: center;
    margin-bottom: 15px;
  }

  &__links {
    display: flex;
    gap: 50px;
    justify-content: center;
    margin-bottom: 105px;
  }

  // &__links-icon {
  //   display: flex;
  //   width: 75px;
  //   height: 75px;
  //   align-items: center;
  //   justify-content: center;
  //   border-radius: 50%;
  //   background-color: #fff;
  //   transition: all .3s;
  //   flex: 0 0 auto;
  //   &:hover {
  //     color: $hover;
  //   }

  //   &:hover svg path {
  //     transition: all .3s;
  //     fill: var(--colorHover);
  //   }
  // }

  &__links-btn {
    max-width: 280px;
    width: 100%;
    height: 80px;
    border-radius: 40px;
    font-size: 2rem;
    line-height: 2.4rem;
    border: none;
    cursor: pointer;
    background-color: #fff;
    color: $main-color;
    transition: all .3s;

    &:hover {
      color: $hover;
    }
  }

  &__arrow {
    display: flex;
    justify-content: center;
    margin-bottom: 200px;


    &-btn {
      background-color: transparent;
      cursor: pointer;
      border: none;
    }
  }

  &__title-form {
    font-size: 4rem;
    line-height: 1.2;
    font-weight: 700;
    text-align: center;
    margin-bottom: 57px;
  }
}

.advantage {
  position: relative;
  margin-bottom: 66px;

  &__title {
    font-size: 4.8rem;
    font-weight: 700;
    line-height: 5.9rem;
    margin-bottom: 60px;
  }

  &__deco {
    position: absolute;
    right: -7%;
    top: 20px;
    width: 30vw;
  }

  &__text {
    display: grid;
    grid-template-columns: auto 124px auto;
    grid-template-rows: 62px 62px;
    font-size: 2.5rem;
    line-height: 3rem;
    gap: 5px;
    margin-left: 120px;
  }

  &__span1 {
    grid-column: 1 / 2;
    grid-row: 1/2;
    display: flex;
    align-items: flex-end;
  }

  &__span2 {
    grid-column: 2 / 3;
    grid-row: 1 / -1;
    background-color: #619DCF;
    color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: 700;
  }

  &__span3 {
    grid-column: 3 / 4;
    grid-row: 2 / 3;
  }

  .container-advantage__text {
    margin-bottom: 97px;
  }

  .container-advantage__slider {
    display: flex;
    gap: 64px;
  }

  .advantage__slider {
    max-width: 377px;

    img {
      width: 100%;
    }
  }

  .container-advantage__plus {
    margin-left: 0;
  }

  &__plus {
    display: flex;
    flex-wrap: wrap;
    gap: 43px;
    max-width: 815px;
  }

  &__plus-card {
    padding: 56px 15px;
    background-color: #d2d2d2;
    border-radius: 20px;
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    max-width: 243px;
    max-height: 232px;
    text-align: center;
    position: relative;
  }

  &__plus-check {
    position: absolute;
    top: -15%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
  }
}

.contacts {

  background-color: var(--colorBlue);

  .slider__item {
    display: flex;
    justify-content: center;
    gap: 70px;
    padding: 50px 20px;
    align-items: center;
  }

  &__block {
    display: block;
    max-width: 280px;
    padding: 10px;
    text-align: center;
    border-radius: 40px;
    font-size: 2rem;
    line-height: 2.4rem;
    border: none;
    cursor: pointer;
    background-color: #fff;
    color: #619dcf;
    transition: all .3s;

    &:hover {
      color: var(--colorHover);
    }
  }
}

.plan {
  margin-top: 50px;
  width: 100vw;

  &__img {
    width: 100%;
    margin-bottom: 45px;

    img {
      width: 100%;
    }
  }

  &__title {
    font-weight: 700;
    line-height: 4.9rem;
    font-size: 4rem;
    text-transform: uppercase;
    margin-bottom: 33px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
  }

  &__title--two {
    margin-bottom: 120px;
  }

  &__list {
    padding-left: 150px;
    font-size: 40px;
    line-height: 49px;
    font-weight: 400;
    margin-bottom: 150px;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-bottom: 33px;
  }

  &__form {
    background-color: #619DCF;
    color: #ffffff;
    margin-bottom: 170px;
  }
}

.bg-block {
  width: 100vw;
  background-color: #619DCF;
  height: 215px;
  margin-bottom: 70px;
}

.plan2 {
  &__slider-img {
    width: 100%;

    img {
      width: 100%;
      max-height: 600px;
      // object-fit: cover;
    }
  }

  &__slider {
    margin-bottom: 150px;
  }

  &__form {
    margin-bottom: 100px;
  }
}

.work {
  width: 100vw;
  margin-bottom: 90px;

  &__bg1 {
    width: 100%;
    background-image: url(../img/content/bg-work.webp);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 75px;
    padding-bottom: 113px;
  }

  &__title {
    font-weight: 700;
    line-height: 59px;
    font-size: 48px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 57px;
  }

  &__block {
    font-weight: 700;
    line-height: 24px;
    font-size: 20px;
    text-align: center;
    padding: 35px 70px;
    background-color: #d2d2d2;
    border-radius: 20px;
    max-width: 745px;
    margin: 0 auto;
    margin-bottom: 57px;
  }

  &__block:nth-of-type(2) {
    max-width: 896px;
  }

  &__block:nth-of-type(3) {
    max-width: 1029px;
  }

  &__block:nth-of-type(4) {
    max-width: 100%
  }

  &__bg2 {
    background-color: #619DCF;
    padding-top: 90px;
    position: relative;
  }

  &__deco {
    position: absolute;
    left: -7%;
    top: 105px;
    width: 30vw;
  }

  &__bg2-title {
    max-width: 785px;
    margin: 0 auto;
    margin-bottom: 50px;
  }

  &__list {
    max-width: 1120px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    color: #ffffff;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 50px;
  }

  &__item-text {
    font-size: 33px;
    line-height: 40px;
    font-weight: 400;
  }

  &__item-img {
    width: 100%;
    max-width: 100%;

    img {
      width: 100%;
    }
  }

  &__bg3 {
    width: 100vw;
    background-image: url(../img/content/bg-work2.webp);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 215px;
  }

}

.worry {
  position: relative;
  margin-bottom: 120px;
  &__title {
    font-weight: 700;
    line-height: 5.9rem;
    font-size: 4.8rem;
    text-align: right;
    margin-bottom: 70px;
  }

  &__deco {
    position: absolute;
    left: -7%;
    top: 20px;
    width: 30vw;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 60px;
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 60px;
  }
  &__item-block {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__item-img {
    width: 100%;
    max-width: 162px;
    img {
      width: 100%;
    }
  }
  &__item-title {
    font-weight: 700;
    line-height: 49px; 
    font-size: 40px;
  }
  &__item-text {
    font-weight: 700;
    font-size: 20px; 
    line-height: 24px;
    text-align: center;
    padding: 80px 40px;
    background-color: #d2d2d2;
    border-radius: 30px;
    max-width: 681px;
  }
}

.about {
  padding-top: 70px;
  margin-bottom: 83px;
  &__title {
    font-size: 40px;
    line-height: 49px;
    font-weight: 700;
    margin-bottom: 60px;
    text-align: center;
  }
  &__map {
    max-width: 710px;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__content {
    display: flex;
    gap: 44px;
  }
  &__contacts {
    display: flex;
    flex-direction: column;
  }
  &__contacts-logo {
    max-width: 98px;
    margin-bottom: 165px;
  }
  &__contacts-title {
    font-weight: 700;
    line-height: 1.5;
    font-size: 32px;
    text-transform: uppercase;
    margin-bottom: 50px;
  }
  &__contacts-phone {
    font-size: 30px;
    font-weight: 400;
    line-height: 22px;
    margin-bottom: 3px;
  }
  &__contacts-time {
    font-size: 20px;
    line-height: 22px;
    font-weight: 400;
    margin-bottom: 46px;
  }
  &__contacts-address {
    font-style: normal;
    font-size: 30px;
    font-weight: 400;
    line-height: 37px;
  }
  &__contacts-email {
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
  }
}

/* _______________ Adaptive _______________ */
/* _______________ max 1100px _______________ */
@media (max-width: 1100px) {
  .advantage {
    &__title {
      text-align: center;
    }

    &__deco {
      display: none;
    }

    &__text {
      font-size: 2rem;
    }
  }

  .work {
    &__deco {
      display: none;
    }

    &__title {
      font-size: 32px;
      line-height: 1.5;
    }

    &__item-img {
      min-width: 200px;
    }

    &__item-text {
      font-size: 20px;
      line-height: 1.5;
    }
  }

  .worry {
    margin-bottom: 60px;
    &__deco {
      display: none;
    }
    &__title {
      text-align: center;
    }
  }
  .about {
    padding-top: 40px;
    margin-bottom: 42px;
    &__title {
      font-size: 28px;
      margin-bottom: 30px;
      line-height: 1.3;
    }
    &__content {
      flex-direction: column;
    }
    &__map {
      max-width: 100%;
    }
    &__contacts-logo {
      align-self: center;
      margin-bottom: 40px;
    }
    &__contacts-title {
      margin-bottom: 20px;
      font-size: 28px;
      line-height: 1.5;
    }
    &__contacts-phone {
      font-size: 20px;
    }
    &__contacts-time {
      font-size: 16px;
      margin-bottom: 20px;
    }
    &__contacts-address {
      font-size: 20px;
      line-height: 1.3;
    }
    &__contacts-email {
      font-size: 16px;
    }
  }
}

/* _______________ max 768px _______________ */
@media(max-width: 768px) {
  .top {
    &__heading {
      text-align: center;
      font-size: 4rem;
      line-height: 1.5;
    }

    &__title-form {
      font-size: 2.8rem;
      line-height: 1.5;
    }

    &__links {
      flex-direction: column;
      align-items: center;
    }
  }

  .form {
    max-width: 350px;

    &__btn {
      font-size: 2.2rem;
      line-height: 1.5;
    }
  }

  .container-advantage__slider {
    flex-direction: column;
    align-items: center;
  }

  .advantage__slider {
    margin: 0 20px;
  }

  .container-advantage__plus {
    margin: 0;
  }

  .advantage__plus {
    align-items: center;
    flex-direction: column;
  }

  .advantage {
    &__text {
      margin-left: 0;
      text-align: center;
      font-size: 1.5rem;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__title {
      font-size: 2.8rem;
      line-height: 1.5;
    }

    &__span2 {
      width: 80px;
      height: 80px;
    }
  }

  .contacts {
    .slider__item {
      display: flex;
      flex-direction: column;
      gap: 35px;

      svg {
        order: 1;
      }
    }
  }

  .plan {
    &__title {
      font-size: 2.4rem;
      line-height: 1.5;
      flex-direction: column;
      text-align: center;

      svg {
        width: 80px;
        height: 80px;
      }
    }

    &__list {
      padding-left: 0;
      font-size: 2.4rem;
      line-height: 1.5;
    }

    &__item {
      gap: 10px;

      &-icon {
        width: 50px;
        height: 50px;
      }
    }

    &__title--three {
      margin-bottom: 100px;
    }
  }

  .plan2 {
    .slider {
      margin-bottom: 80px;
    }
  }

  .work {
    &__title {
      font-size: 28px;
      line-height: 1.5;
    }

    &__item-img {
      min-width: 200px;
    }

    &__item-text {
      font-size: 16px;
      line-height: 1.5;
    }
  }
  .worry {
    &__title {
      font-size: 32px;
      line-height: 1.5;
    }
    &__list {
      gap: 40px
    }
    &__item {
      flex-direction: column;
      gap: 10px;
    }
    &__item-title {
      font-size: 28px;
      line-height: 1.5;
    }
    &__item-text {
      padding: 40px 20px;
      font-size: 16px;
      line-height: 1.5;
      width: 100%;
    }
  }
}

@media(max-width: 600px) {
  .plan {
    &__title {
      margin-bottom: 30px;
    }

    &__img {
      margin-bottom: 90px;
    }

    &__title--two {
      margin-bottom: 30px;
    }

    &__list {
      margin-bottom: 90px;
      font-size: 2rem;
    }

    &__item {
      margin-bottom: 20px;
    }

    &__title--three {
      margin-bottom: 30px;
    }
  }

  .form {
    margin-bottom: 100px;

    &__btn {
      font-size: 1.5rem;
      margin-top: 20px;
    }
  }

  .work {
    &__list {
      gap: 60px;
    }

    &__item {
      flex-direction: column;
      gap: 10px;
    }
  }

}

