/* _______________ FOOTER _______________ */
.footer {
  padding: 20px 0;
  margin-top: 50px;
  box-shadow: 0 0 5px 0 #000000;
  background-color: $footer-background;
  color: $footer-color;

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    gap: 10px;
  }

  &__logo {
    &:hover {
      opacity: 0.7;
    }
  }

  &__social {
    display: flex;
    gap: 15px;
  }

  &__social-item {
    width: 56px;
    height: 56px;
    background-color: #ffffff;
    border-radius: 50%;

    &:hover {
      background-color: $hover;
    }

    .svg-youtube {
      margin-left: 1px;
      margin-right: -1px;
      margin-top: 1px;
      margin-bottom: -1px;
    }
  }
}

@media(max-width: 980px) {
  .footer {
    padding: 15px 0;

    &__social-item {
      padding: 15px;
    }
  }
}

@media(max-width: 600px) {
  .footer {
    &__container {
      flex-direction: column;
      align-items: center;
    }

    &__social {
      order: 1;
      width: 100%;
      justify-content: center;
    }

    &__phone-link {
      font-size: 1.6rem;
      padding: 5px 13px;
      transition: all .3;
    }
  }
}
