/* _______________ GLOBAL STYLES _______________ */
.page {
  font-size: 10px;
  line-height: 1.5;
  background-image: $main-bg;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  display: grid;
  color: $main-color;
  font-size: 1.5rem;
  background-image: $main-bg;
  background-repeat: no-repeat;
  background-size: cover;
  grid-template-rows: 1fr min-content;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

// .main {
//   margin-top: 80px;
// }

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 30px;
}

.container-fluid {
  width: 100vw;
}

.container--inner {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: #79a0c869;
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: #619DCF;
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #3a4276;
}

.form {
  padding: 25px 30px 14px 30px;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  max-width: 401px;
  border: 4px solid var(--colorWhite);
  border-radius: 20px;

  &__label {
    margin-bottom: 10px;
    font-size: 1.7rem;
    line-height: 2.2rem;
  }

  &__input {
    border: 4px solid var(--colorWhite);
    width: 100%;
    padding: 18px 20px;
    border-radius: 30px;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 17px;
    font-size: 1.4rem;
    color: var(--colorWhite);
    outline: none;
    background-color: transparent;

    &:focus {
      border-color: var(--colorHover);
    }
  }

  &__btn {
    margin-top: 50px;
    margin-bottom: 5px;
    background-color: var(--colorWhite);
    font-size: 2.6rem;
    line-height: 2.2rem;
    padding: 15px 25px;
    border-radius: 30px;
    border: 4px solid var(--colorWhite);
    color: #4184DA;
    cursor: pointer;
    outline: none;

    &:focus {
      border-color: var(--colorHover);
    }

    &:hover {
      color: var(--colorHover);
    }
  }

  &__message {
    text-align: center;
    font-size: 1.5rem;
    max-width: 230px;
    align-self: center;
  }
}

.body.lock {
  overflow-y: hidden;
}

.phone {
  transition: all .3;

  &__link {
    font-size: 2rem;
    line-height: 2.4rem;
    padding: 5px 15px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    gap: 5px;
    align-items: center;
    transition: all .3;

    &:hover {
      transition: all .3;
      color: #3f51b5;
    }
  }
}

.link-icon__phone {
  display: flex;
  width: 75px;
  height: 75px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  transition: all .3s;
  flex: 0 0 auto;

  &:hover {
    color: $hover;
  }

  &:hover svg path {
    transition: all .3s;
    fill: var(--colorHover);
  }
}

/* _______________ /GLOBAL STYLES _______________ */